@font-face {
  font-family: "RobotoSlab";
  font-weight: 400;
  src: url('../fonts/RobotoSlab/RobotoSlab-Regular.eot');
  src: url('../fonts/RobotoSlab/RobotoSlab-Regular.svg#RobotoSlab-Regular') format('svg');
  src: url('../fonts/RobotoSlab/RobotoSlab-Regular.otf') format('opentype');
  src: url('../fonts/RobotoSlab/RobotoSlab-Regular.eot?#iefix') format('embedded-opentype');
  src: url('../fonts/RobotoSlab/RobotoSlab-Regular.ttf') format('truetype');
  src: url('../fonts/RobotoSlab/RobotoSlab-Regular.woff') format('woff');
  src: url('../fonts/RobotoSlab/RobotoSlab-Regular.woff2') format('woff2'); }

